
Template.__checkName("ionPopover");
Template["ionPopover"] = new Template("Template.ionPopover", (function() {
  var view = this;
  return HTML.DIV({
    "class": "popover-backdrop"
  }, "\n    ", HTML.DIV({
    "class": "popover-wrapper"
  }, "\n      ", HTML.DIV({
    "class": "popover"
  }, "\n        ", HTML.Raw('<div class="popover-arrow"></div>'), "\n        ", Blaze._InOuterTemplateScope(view, function() {
    return Spacebars.include(function() {
      return Spacebars.call(view.templateContentBlock);
    });
  }), "\n      "), "\n    "), "\n  ");
}));
