
Template.__checkName("ionBody");
Template["ionBody"] = new Template("Template.ionBody", (function() {
  var view = this;
  return HTML.DIV({
    "class": function() {
      return [ "ionic-body ", Spacebars.mustache(view.lookup("platformClasses")) ];
    }
  }, "\n    ", Blaze._InOuterTemplateScope(view, function() {
    return Spacebars.include(function() {
      return Spacebars.call(view.templateContentBlock);
    });
  }), "\n  ");
}));
