
Template.__checkName("ionPopup");
Template["ionPopup"] = new Template("Template.ionPopup", (function() {
  var view = this;
  return HTML.DIV({
    "class": "backdrop"
  }, "\n    ", HTML.DIV({
    "class": "popup-container"
  }, "\n      ", HTML.DIV({
    "class": "popup"
  }, "\n        ", Blaze.If(function() {
    return Spacebars.call(view.lookup("hasHead"));
  }, function() {
    return [ "\n          ", HTML.DIV({
      "class": "popup-head"
    }, "\n            ", Blaze.If(function() {
      return Spacebars.call(view.lookup("title"));
    }, function() {
      return [ "\n              ", HTML.H3({
        "class": "popup-title"
      }, Blaze.View("lookup:title", function() {
        return Spacebars.mustache(view.lookup("title"));
      })), "\n            " ];
    }), "\n            ", Blaze.If(function() {
      return Spacebars.call(view.lookup("subTitle"));
    }, function() {
      return [ "\n              ", HTML.H5({
        "class": "popup-sub-title"
      }, Blaze.View("lookup:subTitle", function() {
        return Spacebars.mustache(view.lookup("subTitle"));
      })), "\n            " ];
    }), "\n          "), "\n        " ];
  }), "\n        ", Blaze.If(function() {
    return Spacebars.call(view.lookup("template"));
  }, function() {
    return [ "\n          ", HTML.DIV({
      "class": "popup-body"
    }, "\n            ", Blaze.View("lookup:template", function() {
      return Spacebars.makeRaw(Spacebars.mustache(view.lookup("template")));
    }), "\n          "), "\n        " ];
  }), "\n        ", Blaze.If(function() {
    return Spacebars.call(Spacebars.dot(view.lookup("buttons"), "length"));
  }, function() {
    return [ "\n          ", HTML.DIV({
      "class": "popup-buttons"
    }, "\n            ", Blaze.Each(function() {
      return Spacebars.call(view.lookup("buttons"));
    }, function() {
      return [ "\n              ", HTML.BUTTON({
        "data-action": "buttonTapped",
        "data-index": function() {
          return Spacebars.mustache(view.lookup("index"));
        },
        "class": function() {
          return [ "button ", Spacebars.mustache(view.lookup("type")) ];
        }
      }, "\n                ", Blaze.View("lookup:text", function() {
        return Spacebars.makeRaw(Spacebars.mustache(view.lookup("text")));
      }), "\n              "), "\n            " ];
    }), "\n          "), "\n        " ];
  }), "\n      "), "\n    "), "\n  ");
}));
