
Template.__checkName("ionRadio");
Template["ionRadio"] = new Template("Template.ionRadio", (function() {
  var view = this;
  return HTML.LABEL({
    "class": "item item-radio"
  }, "\n    ", HTML.INPUT(HTML.Attrs(function() {
    return Spacebars.attrMustache(view.lookup("inputAttrs"));
  })), "\n    ", HTML.DIV({
    "class": "item-content disable-pointer-events"
  }, "\n      ", Blaze._InOuterTemplateScope(view, function() {
    return Spacebars.include(function() {
      return Spacebars.call(view.templateContentBlock);
    });
  }), "\n    "), HTML.Raw('\n    <i class="radio-icon disable-pointer-events icon ion-checkmark"></i>\n  '));
}));
