
Template.__checkName("ionModal");
Template["ionModal"] = new Template("Template.ionModal", (function() {
  var view = this;
  return HTML.DIV({
    "class": "modal-backdrop"
  }, "\n    ", HTML.DIV({
    "class": "modal-wrapper"
  }, "\n      ", HTML.DIV({
    "class": function() {
      return Spacebars.mustache(view.lookup("classes"));
    }
  }, "\n\n        ", Blaze.If(function() {
    return Spacebars.call(view.lookup("customTemplate"));
  }, function() {
    return [ "\n          ", Blaze._InOuterTemplateScope(view, function() {
      return Spacebars.include(function() {
        return Spacebars.call(view.templateContentBlock);
      });
    }), "\n        " ];
  }, function() {
    return [ "\n          ", HTML.DIV({
      "class": function() {
        return Spacebars.mustache(view.lookup("barClass"));
      }
    }, "\n            ", HTML.H2({
      "class": function() {
        return Spacebars.mustache(view.lookup("titleClass"));
      }
    }, Blaze.View("lookup:title", function() {
      return Spacebars.mustache(view.lookup("title"));
    })), "\n            ", Blaze.If(function() {
      return Spacebars.call(view.lookup("closeText"));
    }, function() {
      return [ "\n              ", HTML.BUTTON({
        "data-dismiss": "modal",
        "class": "button button-positive button-clear"
      }, Blaze.View("lookup:closeText", function() {
        return Spacebars.mustache(view.lookup("closeText"));
      })), "\n            " ];
    }, function() {
      return [ "\n              ", HTML.BUTTON({
        "data-dismiss": "modal",
        "class": "button button-icon"
      }, HTML.I({
        "class": "icon ion-ios-close-empty"
      })), "\n            " ];
    }), "\n          "), "\n          ", HTML.DIV({
      "class": "content has-header overflow-scroll"
    }, "\n            ", Blaze._InOuterTemplateScope(view, function() {
      return Spacebars.include(function() {
        return Spacebars.call(view.templateContentBlock);
      });
    }), "\n          "), "\n        " ];
  }), "\n\n      "), "\n    "), "\n  ");
}));
