
Template.__checkName("ionNavBar");
Template["ionNavBar"] = new Template("Template.ionNavBar", (function() {
  var view = this;
  return HTML.DIV({
    "class": function() {
      return [ Spacebars.mustache(view.lookup("classes")), " nav-bar-block nav-bar-transition-", Spacebars.mustache(view.lookup("transition")), " nav-bar-direction-forward" ];
    },
    "data-navbar-container": ""
  }, "\n    ", Blaze._TemplateWith(function() {
    return "headerButtonLeft";
  }, function() {
    return Spacebars.include(view.lookupTemplate("yield"));
  }), "\n    ", Blaze._TemplateWith(function() {
    return "headerTitle";
  }, function() {
    return Spacebars.include(view.lookupTemplate("yield"));
  }), "\n    ", Blaze._TemplateWith(function() {
    return "headerButtonRight";
  }, function() {
    return Spacebars.include(view.lookupTemplate("yield"));
  }), "\n  ");
}));
