
Template.__checkName("ionContent");
Template["ionContent"] = new Template("Template.ionContent", (function() {
  var view = this;
  return HTML.DIV({
    "class": "scroll-content ionic-scroll"
  }, "\n    ", HTML.DIV({
    "class": function() {
      return Spacebars.mustache(view.lookup("classes"));
    }
  }, "\n      ", Blaze._InOuterTemplateScope(view, function() {
    return Spacebars.include(function() {
      return Spacebars.call(view.templateContentBlock);
    });
  }), "\n    "), "\n  ");
}));
