
Template.__checkName("ionTab");
Template["ionTab"] = new Template("Template.ionTab", (function() {
  var view = this;
  return HTML.A({
    "class": function() {
      return [ Spacebars.mustache(view.lookup("classes")), " ", Spacebars.mustache(view.lookup("isActive")) ];
    },
    href: function() {
      return Spacebars.mustache(view.lookup("url"));
    },
    "data-ion-tab": ""
  }, "\n    ", Blaze.If(function() {
    return Spacebars.call(view.lookup("defaultIcon"));
  }, function() {
    return [ "\n      ", Blaze.If(function() {
      return Spacebars.call(view.lookup("isActive"));
    }, function() {
      return [ "\n        ", Blaze._TemplateWith(function() {
        return {
          icon: Spacebars.call(view.lookup("activeIcon"))
        };
      }, function() {
        return Spacebars.include(view.lookupTemplate("ionIcon"));
      }), "\n      " ];
    }, function() {
      return [ "\n        ", Blaze._TemplateWith(function() {
        return {
          icon: Spacebars.call(view.lookup("defaultIcon"))
        };
      }, function() {
        return Spacebars.include(view.lookupTemplate("ionIcon"));
      }), "\n      " ];
    }), "\n    " ];
  }), "\n\n    ", Blaze.View("lookup:title", function() {
    return Spacebars.mustache(view.lookup("title"));
  }), "\n\n    ", Blaze.If(function() {
    return Spacebars.call(view.lookup("badgeNumber"));
  }, function() {
    return [ "\n      ", HTML.DIV({
      "class": function() {
        return [ "badge badge-", Spacebars.mustache(view.lookup("badgeColor")) ];
      }
    }, "\n        ", Blaze.View("lookup:badgeNumber", function() {
      return Spacebars.mustache(view.lookup("badgeNumber"));
    }), "\n      "), "\n    " ];
  }), "\n  ");
}));
