
Template.__checkName("ionItem");
Template["ionItem"] = new Template("Template.ionItem", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.call(view.lookup("isAnchor"));
  }, function() {
    return [ "\n    ", HTML.A({
      "class": function() {
        return Spacebars.mustache(view.lookup("itemClasses"));
      },
      href: function() {
        return Spacebars.mustache(view.lookup("url"));
      },
      id: function() {
        return Spacebars.mustache(view.lookup("idAttribute"));
      },
      target: function() {
        return Spacebars.mustache(view.lookup("target"));
      }
    }, "\n      ", Blaze._InOuterTemplateScope(view, function() {
      return Spacebars.include(function() {
        return Spacebars.call(view.templateContentBlock);
      });
    }), "\n    "), "\n  " ];
  }, function() {
    return [ "\n    ", HTML.DIV({
      "class": function() {
        return Spacebars.mustache(view.lookup("itemClasses"));
      },
      id: function() {
        return Spacebars.mustache(view.lookup("idAttribute"));
      }
    }, "\n      ", Blaze._InOuterTemplateScope(view, function() {
      return Spacebars.include(function() {
        return Spacebars.call(view.templateContentBlock);
      });
    }), "\n    "), "\n  " ];
  });
}));
