
Template.__checkName("ionActionSheet");
Template["ionActionSheet"] = new Template("Template.ionActionSheet", (function() {
  var view = this;
  return HTML.DIV({
    "class": "action-sheet-backdrop"
  }, "\n    ", HTML.DIV({
    "class": "action-sheet-wrapper"
  }, "\n      ", HTML.DIV({
    "class": "action-sheet"
  }, "\n        ", HTML.DIV({
    "class": "action-sheet-group"
  }, "\n          ", Blaze.If(function() {
    return Spacebars.call(view.lookup("titleText"));
  }, function() {
    return [ "\n            ", HTML.DIV({
      "class": "action-sheet-title"
    }, "\n              ", Blaze.View("lookup:titleText", function() {
      return Spacebars.mustache(view.lookup("titleText"));
    }), "\n            "), "\n          " ];
  }), "\n          ", Blaze.Each(function() {
    return Spacebars.call(view.lookup("buttons"));
  }, function() {
    return [ "\n            ", HTML.BUTTON({
      "class": "button",
      "data-index": function() {
        return Spacebars.mustache(view.lookup("index"));
      }
    }, Blaze.View("lookup:text", function() {
      return Spacebars.makeRaw(Spacebars.mustache(view.lookup("text")));
    })), "\n          " ];
  }), "\n        "), "\n        ", Blaze.If(function() {
    return Spacebars.call(view.lookup("destructiveText"));
  }, function() {
    return [ "\n          ", HTML.DIV({
      "class": "action-sheet-group"
    }, "\n            ", HTML.BUTTON({
      "class": "button destructive",
      "data-destructive": ""
    }, Blaze.View("lookup:destructiveText", function() {
      return Spacebars.makeRaw(Spacebars.mustache(view.lookup("destructiveText")));
    })), "\n          "), "\n        " ];
  }), "\n        ", Blaze.If(function() {
    return Spacebars.call(view.lookup("cancelText"));
  }, function() {
    return [ "\n          ", HTML.DIV({
      "class": "action-sheet-group"
    }, "\n            ", HTML.BUTTON({
      "class": "button",
      "data-cancel": ""
    }, Blaze.View("lookup:cancelText", function() {
      return Spacebars.makeRaw(Spacebars.mustache(view.lookup("cancelText")));
    })), "\n          "), "\n        " ];
  }), "\n      "), "\n    "), "\n  ");
}));
