
Template.__checkName("ionNavView");
Template["ionNavView"] = new Template("Template.ionNavView", (function() {
  var view = this;
  return HTML.DIV({
    "data-nav-container": "",
    "class": function() {
      return [ "nav-view-transition-", Spacebars.mustache(view.lookup("transition")), " nav-view-direction-forward" ];
    }
  }, "\n    ", Blaze._InOuterTemplateScope(view, function() {
    return Spacebars.include(function() {
      return Spacebars.call(view.templateContentBlock);
    });
  }), "\n  ");
}));
