
Template.__checkName("ionLoading");
Template["ionLoading"] = new Template("Template.ionLoading", (function() {
  var view = this;
  return HTML.DIV({
    "class": "loading-container"
  }, "\n    ", HTML.DIV({
    "class": "loading"
  }, "\n      ", Blaze.If(function() {
    return Spacebars.call(view.lookup("template"));
  }, function() {
    return [ "\n        ", Blaze.View("lookup:template", function() {
      return Spacebars.makeRaw(Spacebars.mustache(view.lookup("template")));
    }), "\n      " ];
  }, function() {
    return [ "\n        ", HTML.I({
      "class": "icon ion-loading-d"
    }), "\n      " ];
  }), "\n    "), "\n  ");
}));
