
Template.__checkName("ionSpinner");
Template["ionSpinner"] = new Template("Template.ionSpinner", (function() {
  var view = this;
  return HTML.I({
    "class": function() {
      return [ "spinner ", Spacebars.mustache(view.lookup("classes")), " ", Spacebars.mustache(view.lookup("icon")) ];
    }
  });
}));
