
Template.__checkName("ionView");
Template["ionView"] = new Template("Template.ionView", (function() {
  var view = this;
  return [ Blaze.If(function() {
    return Spacebars.call(view.lookup("title"));
  }, function() {
    return [ "\n    ", Blaze._TemplateWith(function() {
      return "headerTitle";
    }, function() {
      return Spacebars.include(view.lookupTemplate("contentFor"), function() {
        return [ "\n      ", HTML.H1({
          "class": "title"
        }, Blaze.View("lookup:title", function() {
          return Spacebars.mustache(view.lookup("title"));
        })), "\n    " ];
      });
    }), "\n  " ];
  }), "\n\n  ", HTML.DIV({
    "class": function() {
      return Spacebars.mustache(view.lookup("classes"));
    }
  }, "\n    ", Blaze._InOuterTemplateScope(view, function() {
    return Spacebars.include(function() {
      return Spacebars.call(view.templateContentBlock);
    });
  }), "\n  ") ];
}));
