
Template.__checkName("ionNavBackButton");
Template["ionNavBackButton"] = new Template("Template.ionNavBackButton", (function() {
  var view = this;
  return HTML.BUTTON({
    "class": function() {
      return Spacebars.mustache(view.lookup("classes"));
    }
  }, "\n    ", Blaze._TemplateWith(function() {
    return {
      icon: Spacebars.call(view.lookup("icon"))
    };
  }, function() {
    return Spacebars.include(view.lookupTemplate("ionIcon"));
  }), "\n    ", Blaze.Unless(function() {
    return Spacebars.call(view.lookup("isAndroid"));
  }, function() {
    return [ "\n      ", HTML.SPAN({
      "class": "back-text"
    }, "\n        ", Blaze.View("lookup:text", function() {
      return Spacebars.mustache(view.lookup("text"));
    }), "\n      "), "\n    " ];
  }), "\n  ");
}));
